wishlist {
  > .title {
    font-size: 3rem;
    text-align: center;
    margin: 10px 0;
  }

  ol.wishes {
    margin: 0;
    padding: 0;

    column-count: 2;

    @media(min-width: $screen-sm-min) {
      column-count: 3;
    }

    @media(min-width: $screen-md-min) {
      column-count: 4;
    }

    li {
      margin: 0;
      padding: 0;
      display: inline-block;
      width: 100%;
      margin-bottom: 10px;
      padding: 10px;
      background-color: #fff;

      wish {
        &.reserved * {
          opacity: 0.5;
          transition: opacity 1s;
        }

        .status {
          text-transform: uppercase;
          text-align: center;
          color: $brand-primary;
          font-weight: bold;
          font-size: 1.7rem;
          opacity: 0.7;
          margin-bottom: 0;
        }

        > img {
          width: 100%;
          height: auto;
          margin-bottom: 10px;
        }

        > .title {
          color: $brand-primary;
          font-size: 1.5rem;
          font-weight: bold;
          text-align: center;
        }

        > .actions {
          margin-top: 5px;

          > div {
            @extend .btn-group;

            > button {
              @extend .btn;
              @extend .btn-primary;
            }
          }
        }
      }
    }
  }
}
